import faker from 'faker';
import { call, delay, put } from 'redux-saga/effects';
import shortId from 'shortid';
import * as authAPI from '../lib/api/auth';
import { finishLoading, startLoading } from '../reducers/loading';

export const createRequestActionTypes = (type) => {
  const REQUEST = `${type}_REQUEST`;
  const SUCCESS = `${type}_SUCCESS`;
  const FAILURE = `${type}_FAILURE`;
  return [REQUEST, SUCCESS, FAILURE];
};

export function createRequestSaga(type, request) {
  const SUCCESS = type.replace(/REQUEST/g, 'SUCCESS');
  const FAILURE = type.replace(/REQUEST/g, 'FAILURE');

  return function* (action) {
    yield put(startLoading(type)); // 
    // action       .
    try {
      const response = yield call(request, action.payload); //api 
      //console.log('api  : ', type, action);
      //console.log('response: ', response, 'success', SUCCESS, 'faliure', FAILURE); //promise  return  ..

      yield put({
        type: SUCCESS,
        payload: response.data,
      });
    } catch (e) {
      const errorData = e.response;
      console.error('error', e);

      yield put({
        type: FAILURE,
        payload: errorData,
        error: true,
      });
    }
    yield put(finishLoading(type)); // 
  };
}

export function* oauthLogin(action) {
  //console.log('?' + action);

  try {
    const response = yield call(authAPI.socialLogin, action.data);
    //console.log('response: ', response);
    yield put({
      type: 'LOGIN_SUCCESS',
      payload: response.data,
    });
  } catch (err) {
    console.error(err);
    yield put({
      type: 'LOGIN_FAILURE',
      payload: err.response.data,
      error: true,
    });
  }
}

export default function createFakeRequestSaga(type, request) {
  const SUCCESS = type.replace(/REQUEST/g, 'SUCCESS');
  const FAILURE = type.replace(/REQUEST/g, 'FAILURE');
  //console.log('   ', aa);

  return function* (action) {
    yield put(startLoading(type)); // 
    // action       .

    try {
      //const response = yield call(request, action.payload); //api 
      //console.log('api  : ', type, action);
      yield delay(100);

      console.log('fake Data:', generateDummyPost(action.payload));

      yield put({
        type: SUCCESS,
        payload: generateDummyPost(action.payload),
      });
    } catch (e) {
      const errorData = e.response;
      console.error('errorData', errorData);

      yield put({
        type: FAILURE,
        payload: errorData,
        error: true,
      });
    }
    yield put(finishLoading(type)); // 
  };
}

const generateDummyPost = (number) =>
  Array(number)
    .fill()
    .map(() => ({
      id: shortId.generate(),
      User: {
        id: shortId.generate(),
        nickname: faker.name.findName(),
      },
      title: faker.name.title(),
      content: faker.lorem.paragraph(),
      thumbnail: faker.image.image(),
      createDate: new Date().getFullYear() + '-' + (new Date().getMonth() + 1) + '-' + new Date().getDate(),
      likeCount: Math.floor(Math.random() * 100),
    }));
