import axios from 'axios';

const client = axios.create();

//   : https://fkkmemi.github.io/nemv/nemv-053-axios-interceptor/

// //API 
//client.defaults.baseURL = 'http://localhost:8080/';
// // 
client.defaults.headers.common['Authorization'] = 'Bearer a1b2c3d4';

client.defaults.headers.post['Content-Type'] = 'application/json; charset=UTF-8'; //json  ..
client.defaults.headers.put['Content-Type'] = 'application/json; charset=UTF-8'; //json  ..

//client.defaults.headers.common['Authorization'] = localStorage.getItem('accessToken'); //  ..  .

client.interceptors.request.use(
  (request) => {
    //request.headers.common.Authorization = 'Bearer ' + localStorage.getItem('accessToken');
    //console.log('Starting Request', JSON.stringify(request, null, 2));
    console.log('Starting Request', request);
    return request;
  },
  (error) => {
    //   
    return Promise.reject(error);
  },
);

// 
client.interceptors.response.use(
  (response) => {
    //     
    //console.log(': ', response.data);
    //response.headers['withCredentials'] = true;
    console.log(response);
    return response;
  },
  (error) => {
    //     
    console.error('error: ', error);
    return Promise.reject(error);
  },
);

export default client;
