import React, { memo } from 'react';
import { StyledHeadDescDiv } from '../pages/user/style';
import { HeartOutlined, HeartFilled } from '@ant-design/icons';
import { useCallback } from 'react';
import { useDispatch } from 'react-redux';
import { getPostAction, likeDeleteAction, likePostAction, postDeleteAction } from '../reducers/post';
import { Link, useHistory } from 'react-router-dom';
import useUpdateEffect from '../lib/hooks/useUpdateEffect';
import { useState } from 'react';
import moment from 'moment';
import { StyledDetailTagLink, StyledLikeBtn, StyledPostDetailTagDiv, StyledUnlikeBtn } from './style';
import { Button } from 'antd';
import { useEffect } from 'react';
import { useSelector } from 'react-redux';

const PostDetailHeader = memo((props) => {
  const { userId, postId, principal, postDeleteDone, postDeleteError } = props;

  const { post, likePostDone, likePostError, likeDeleteDone, likeDeleteError } = useSelector(({ post }) => ({
    post: post?.post,
    likePostDone: post?.likePostDone,
    likePostError: post?.likePostError,
    likeDeleteDone: post?.likeDeleteDone,
    likeDeleteError: post?.likeDeleteError,
  }));

  let history = useHistory();

  const dispatch = useDispatch();
  //  redux data   ..    .. ....
  const [likeState, setLikeState] = useState(null);
  const [likeCount, setLikeCount] = useState(null);

  useEffect(() => {
    setLikeState(post?.likeState); // ......
    setLikeCount(post?.likeCount);
    //console.log('likeState', post.likeState);
  }, [post]);

  useUpdateEffect(() => {
    if (likePostError) {
      alert('  .');
    }

    // 
    if (likePostDone) {
      //console.log('  ..  useUpdateEffect 4   ,   ,     ');
      //setLikeState(true);
      //setLikeCount(likeCount + 1);

      dispatch(getPostAction({ userId, postId }));
      //console.log('dispatch  redux store          ..', post);

      setLikeState(post?.likeState);
      setLikeCount(post?.likeCount);
    }
  }, [likePostError, likePostDone]);

  useUpdateEffect(() => {
    // 
    if (likeDeleteDone) {
      dispatch(getPostAction({ userId, postId }));
      //console.log('   ..', post);
      setLikeState(post?.likeState);
      setLikeCount(post?.likeCount);
    }

    if (likeDeleteError) {
      //  principal    response data msg      
      alert('  .');
    }
  }, [likeDeleteDone, likeDeleteError]);

  useUpdateEffect(() => {
    if (postDeleteDone) {
      alert('  .');
      history.replace(`/${principal.id}`);
    }

    // 
    if (postDeleteError) {
      alert('  .');
    }
  }, [postDeleteDone, postDeleteError]);

  const onLike = useCallback(() => {
    //console.log('like btn ', postId);
    dispatch(likePostAction(postId));
  }, [dispatch, postId]);

  const onUnLike = useCallback(() => {
    console.log('unlike btn ', postId);

    dispatch(likeDeleteAction(postId));
  }, [dispatch, postId]);

  const useConfirm = (message = null, onConfirm, onCancel) => {
    if (!onConfirm || typeof onConfirm !== 'function') {
      return;
    }
    if (onCancel && typeof onCancel !== 'function') {
      return;
    }

    const confirmAction = () => {
      if (window.confirm(message)) {
        onConfirm();
      } else {
        onCancel();
      }
    };

    return confirmAction;
  };
  const deleteConfirm = useCallback(() => {
    console.log('  ', postId);
    dispatch(postDeleteAction(postId));
  }, [dispatch, postId]);

  const cancelConfirm = () => console.log('.');
  const confirmDelete = useConfirm(' ?', deleteConfirm, cancelConfirm);

  const updatePost = () => {
    console.log('props', props);
    console.log('principal', principal, 'post', postId);

    history.push(`update/${postId}`); //  ?
    //history.replace(`update?userId=${userId}&postId=${postId}`);

    //dispatch(getPostAction(userId, postId));
  };

  return (
    <>
      <div className="head-wrapper">
        <h1>{post?.title}</h1>

        <StyledHeadDescDiv>
          <div className="information">
            <span className="username">
              <Link to={userId && `/${userId}`}>{post?.user?.username}</Link>
            </span>
            <span className="separator" style={{ marginLeft: '1rem' }}>
              
            </span>
            <span style={{ marginLeft: '1rem' }}>{moment(post?.createDate).format('YYYY MM DD')}</span>
          </div>

          {principal != null && userId == principal.id ? (
            <div className="btn-box">
              <Button type="dashed" shape="round" onClick={confirmDelete}>
                
              </Button>
              <Button type="dashed" shape="round" onClick={updatePost}>
                
              </Button>
            </div>
          ) : null}

          <div>
            {likeState ? (
              <StyledLikeBtn onClick={onUnLike}>
                <HeartFilled key="heart" />
                <span>{likeCount}</span>
              </StyledLikeBtn>
            ) : (
              <StyledUnlikeBtn onClick={onLike}>
                <HeartOutlined key="heart" />
                <span>{likeCount}</span>
              </StyledUnlikeBtn>
            )}
          </div>
        </StyledHeadDescDiv>
        <StyledPostDetailTagDiv>
          {post.tags.map((tag) => (
            <StyledDetailTagLink key={tag?.id} to={`/tag?name=${tag?.name}`}>
              {tag?.name}
            </StyledDetailTagLink>
          ))}
        </StyledPostDetailTagDiv>
      </div>
    </>
  );
});

export default PostDetailHeader;
